@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400;500;700&family=EB+Garamond:wght@400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:"Cormorant Garamond",serif;background:#012f5a;background:linear-gradient(270deg, #012f5a 12%, #c1d4e1 28%, #c1d4e1 72%, #012f5a 88%);color:#fff;text-align:center}.app{max-width:960px;margin:auto;position:relative;background-color:#111213}.aside{padding:20px;background:#e9e9e9;background:linear-gradient(180deg, #e9e9e9 75%, #fff 100%)}.card{display:grid;grid-template-columns:1fr 4fr}.card__avatar{padding:20px}.card__btn{margin:0 20px;width:140px;height:26px;color:#fff;background:#2C83B0;border-radius:4px;border:none;margin-bottom:15px}.card__btn :focus{outline:none}.card__container{text-align:left}.card__date{color:#2C83B0;font-size:14px;font-family:"Cormorant Garamond",serif;line-height:0;font-weight:bold}.card__paragraph{padding:0 20px;color:#6F6E6E;font-family:"EB Garamond",serif;font-size:13px;margin-bottom:8px}.card__title{font-family:"Cormorant Garamond",serif;color:#303030;font-size:25px;font-weight:normal;line-height:16px}.card__titleBox{margin:20px 0 10px 20px}.content{background-color:#fff;display:grid;grid-template-columns:1fr 250px}.footer{display:grid;grid-template-columns:repeat(3, 1fr);background-color:#111213}.footer a{text-decoration:none;color:#6F6E6E}.footer__container{margin:20px}.footer__box{display:grid;grid-template-columns:repeat(3, 1fr);align-content:center;justify-content:center}.footer__paragraph,.footer__special{color:#6F6E6E;font-family:"EB Garamond",serif;font-size:13px;line-height:20px;margin:0}.footer__socials{padding-top:20px}.footer__special{font-weight:bold;color:#A3A3A3}.footer__title{font-family:"Cormorant Garamond",serif;color:#fff;font-size:25px;font-weight:normal}.hamburger{display:none}.header{background-color:#111213;display:flex;height:auto;margin-top:-2px;width:100%;position:relative}.header a{color:#ffffff;font-family:"Cormorant Garamond",serif;font-size:12px;font-weight:bold;font-style:normal;letter-spacing:normal;line-height:normal;text-transform:uppercase;text-decoration:none;padding:5px;padding-right:18px;transition:all 0.2s ease-in-out;border:none;outline:none;background-color:#111213}.header a:hover{text-decoration:none;color:#fff;transform:scale(1.3)}.header a:focus{outline:none}.header__bottom{max-width:100%;height:150px;background-color:#111213;margin-top:-1%;display:grid;grid-template-columns:2fr 1fr;justify-content:center;align-items:center}.header__logo{width:400px;height:auto;padding:27px 34px}.header__navigation{top:70px;right:20px;position:absolute;display:inline-block;width:auto}.header__search{width:125px;height:19px;color:#8a8a8a;font-family:"EB Garamond",serif;font-size:10px;border-radius:16px;box-shadow:inset 1px 2px 5px rgba(0,0,0,0.35);border:none;padding-left:24px;position:relative}.header__searchBox{position:absolute;top:20px;right:19px}.header__search--ico,.header__search--ico:focus{width:28px;height:28px;outline:none;background:none;position:absolute;left:-2px;top:-1px;border:none}.header__quotes{color:#A3A3A3;font-family:"Cormorant Garamond",serif;text-align:left;font-size:30px;margin-left:34px}.news{margin-bottom:40px}.news__title{font-family:"Cormorant Garamond",serif;color:#303030;font-size:25px;font-weight:normal}.news__paragraph{color:#6F6E6E;font-family:"EB Garamond",serif;font-size:13px;margin-bottom:4px}.news__paragraph--link,.news__paragraph a{text-decoration:none;color:#2C83B0}.sponsors{background:#fff;margin:0}.sponsors__box{display:flex;flex-wrap:wrap;align-content:center;justify-content:center}.sponsors__img{margin:0 20px}.sponsors__title{font-family:"Cormorant Garamond",serif;color:#303030;font-size:25px;font-weight:normal;line-height:30px;padding:20px}@media screen and (max-width: 768px){.app{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.card{grid-template-columns:1fr}.card__box{flex-direction:column}.card__btn{margin-top:10px}.card__container{text-align:center;padding:8px 5px;margin:0 auto;width:80%}.card__section{text-align:center}.content{grid-template-columns:1fr}.footer{grid-template-columns:1fr}.footer__container{text-align:center;width:65%;margin:20px auto}.header__bottom{height:auto;padding-top:15px;text-align:center;grid-template-columns:1fr;width:100%}.header__logo{padding-top:30px;margin:0}.header__navigation{padding-top:0;position:static;position:initial;display:block;padding-bottom:27px}.header__socials{width:50%;height:auto}.header__quotes{font-size:18px;text-align:center;width:80%}.hamburger{display:none}.pagination{display:flex;justify-content:center}}@media screen and (max-width: 375px){body{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.header__bottom{padding:10px 0;display:flex;flex-direction:column}.header__logo{padding-top:70px}.header__navigation{display:none}.navigation--active{display:flex;flex-direction:column;position:absolute;width:-webkit-max-content;width:-moz-max-content;width:max-content;left:0px;top:146px}.hamburger,.hamburger:focus{display:inline-block;background-color:transparent;border:0;margin:0;outline:0;text-align:left;margin:20px;padding-top:20px}.hamburger__box{width:35px;height:24px;display:inline-block;position:relative}.hamburger__inner{width:100%;height:3px;background-color:#fff;position:absolute;left:0;top:50%;transform:translateY(-50%);transition:transform 0.3s 0.2s erase-in-out}.hamburger__inner::before,.hamburger__inner::after{content:"";width:100%;height:3px;background-color:#fff;position:absolute;left:0;transition:transform 0.3s 0.2s erase-in-out}.hamburger__inner::before{top:-10px}.hamburger__inner::after{top:10px}.hamburger--active .hamburger__inner{background-color:transparent}.hamburger--active .hamburger__inner::after{transform:translateY(-10px) rotate(-45deg)}.hamburger--active .hamburger__inner::before{transform:translateY(10px) rotate(45deg)}}

